import { defineComponent } from '@vue/runtime-core';
import axios from 'axios';
import moment from 'moment';
import { Test } from '@interface/Medical/Test.interface';
import { ConsultPagination } from '@interface/Medical/ConsultPagination.interface';
import { ConsultPaginationData } from '@data/Medical/ConsultPagination.data';
import { Consult } from '@interface/Medical/Consult.interface';

/** 
 * @description Componente que muestra a enfermera la lista de estudios mdicos creados y su estado actual, mediante el uso de una tablq
 * @class TestNurseTableComponent
 * @example <test-nurse-component :title="" :role=""></test-nurse-component>
*/
export default defineComponent({
    /** 
     * Propiedades que recibe el componente 
     * @member TestNurseTableComponent.props
     * @property {string} title (Opcional) Ttulo de la tabla
     * @property {string} role (Obligatorio) Rol del usuario logueado actualmente
    */
    props: {
        title: {
            type: String,
            default: ''
        },
        role: {
            type: String,
            default: ''
        }
    },
    /**
    * Variables del componente
    * @member TestNurseTableComponent.data
    * @property {ConsultPagination} consultData Guarda la informacin de los estudios mdicos que retorna el servidor mediante paginacin
    * @property {number} paginationPages Guarda el nmero de pginas de userData para mostrar el nmero de pginas en la paginacin de la tabla
    * @property {number} paginationActive Guarda el nmero de pgina activa de la tabla
    * @property {string} query Guarda la bsqueda realizada en el input de bsqueda de la tabla
    * @property {boolean} activateSearch Activa o no la bsqueda desde el input de bsqueda de la tabla
    * @property {boolean} loading Activa o no la animacin de carga dentro de la tabla
    */
    data() {
        return {
            consultData: ConsultPaginationData,
            paginationPages: 0,
            paginationActive: 0,
            query: '',
            activateSearch: true,
            loading: true
        };
    },
    /** 
     * Al iniciar el componente, se obtienen los datos de los {@link TestNurseTableComponent.getUserData|estudios mdicos}
     * @member TestNurseTableComponent.mounted
    */
    mounted() {
        this.getUserData(1);
    },
    /**
    * Propiedades computadas del componente 
    * @member TestNurseTableComponent.computed
    * @property {boolean} showSupplierCode Muestra u oculta la columna de Cdigo de proveedor en caso de existir o no dicho cdigo en alguno de los estudios mdicos 
    */
    computed: {
        showSupplierCode(): boolean
        {
            return this.consultData.data.filter(item => item.test_scheduled!.order?.product?.supplier_code).length > 0;
        }
    },
    methods: {
        /** 
         * Formatea la fecha provista por el servidor a un formato local
         * @param {string} date Fecha de la consulta donde se cre el estudio
         * @function TestNurseTableComponent.formatDate
        */
        formatDate(date: string)
        {
            return moment(date).format('DD-MM-YYYY');
        },
        /** 
         * Concatena el nombre completo del paciente
         * @param {Consult} consult Datos de la consulta
         * @function TestNurseTableComponent.formatPatientName
        */
        formatPatientName(consult: Consult)
        {
            return `${consult.patient!.first_name} ${consult.patient!.last_name}`;
        },
        /** 
         * Obtiene la lista los estudios mdicos creados, provistas por el servidor.
         * Antes de realizar la peticin al servidor dicha lista, se habilita la animacin de carga con la asignacin true de la variable loading, se verifica que la pgina guardada en la variable page sea mayor o igual a 1,
         * que la pgina sea menor o igual a la ltima pgina de la paginacin guardada en la variable
         * consultData y que la pgina no sea la misma que la de la variable paginationActive.
         * Si la comprobacin es exitosa se realiza una peticin HTTP a la URI /consultas/muestras con el parmetro de la pgina. Si la peticin es correcta se asigna la variable consultData con la respuesta
         * del servidor, se asigna a la variabler paginationPages con los datos de la paginacin provista por el servidor y se deshabilita la animacin de carga mediante la asignacin false de la variable loading,
         * en caso contrario solo se deshabilita la animacin de la carga mediante la asignacin false de la variable loading
         * @param {number} page Nmero de la pgina
         * @function TestNurseTableComponent.getUserData
        */
        getUserData(page: number)
        {
            this.loading = true;
            if(page >= 1 && page <= this.consultData.pagination.last_page && page !== this.paginationActive)
            {
                this.paginationActive = page;
                axios.get<ConsultPagination>(`/consultas/muestras?page=${this.paginationActive}`)
                .then(response => {
                    this.consultData = response.data;
                    this.paginationPages = response.data.pagination.last_page;
                    this.loading = false;
                })
                .catch(error => {
                    
                    this.loading = false;
                })
            }
        },
        /** 
         * Obtiene la lista de estudios mdicos que coincidan con la bsqueda realizada por el usuario.
         * Antes de realizar la peticin al servidor con los empleados, se habilita la animacin de carga con la asignacin true de la variable loading,
         * se asigna a la variable queryPagination el valor de la variable paginationActive si la variable query est vaco, en caso contrario se le asigna el 0.
         * Si la variable activateSeach es true o la longitud de la variable query es mayor a 0, se realizar una llamada a la URI /consultas/muestras con el parmetro de la pgina y el contenido de la bsqueda.
         * Si la peticin es correcta se asigna la variable consultData con la respuesta del servidor, se asigna a la variabler paginationPages con los datos de la paginacin provista por el servidor
         * y se deshabilita la animacin de carga mediante la asignacin false de la variable loading, en caso contrario solo se deshabilita la animacin de la carga mediante la asignacin false de la variable loading
         * @function TestNurseTableComponent.getUserDataQuery
        */
        getUserDataQuery()
        {
            this.loading = true;
            const queryPagination = this.query === '' ? this.paginationActive : 0;
            if(this.activateSearch || this.query.length > 0)
            {
                axios.get<ConsultPagination>(`/consultas/muestras`, {
                    params: {
                        page: queryPagination,
                        query: this.query
                    }
                })
                .then(response => {
                    this.consultData = response.data;
                    this.paginationPages = response.data.pagination.last_page;
                    this.loading = false;
                })
                .catch(error => {
                    
                    this.loading = false;
                })
            }
            this.activateSearch = this.query === '' ? false : true;
        },
    },
})